import React, { Component } from 'react';
import Url from './../Url/Url';
import CopyButton from './../CopyButton/CopyButton';

import './App.css';

export default class App extends Component {

    constructor() {
        super();
        this.state = {
            bgColor: 'F52A19',
            textColor: '000'
        };
        this.handleInput = this.handleInput.bind(this);
    }

    handleInput(e) {
        let newColor = e.target.value.replace('#', ' ');

        this.setState({
            bgColor: newColor,
            textColor: (parseInt(newColor, 16) > 0xffffff / 2) ? '000' : 'fff'
        });
    }

    render() {
        return (
            <div className="bg" style={{backgroundColor: "#" + this.state.bgColor, color: "#" + this.state.textColor}}>
                <div className="container">
                    <Url bgColor={this.state.bgColor} changeColor={this.handleInput} />

                    <div className="content">
                        <p className="info">
                            Simple image placeholder. Just choose the size, color and copy the url to your code. 
                            Created by <a href="https://sammarxz.com" target="blank">@sammarxz</a>. 
                            Feel free to collaborate on <a href="https://github.com/sammarz/picsum.co" target="blank">GitHub</a>.
                        </p>
                        <CopyButton />
                    </div>
                </div>
            </div>
        )
    }
}
