import React, { Component } from 'react';
import './Buttons.css';

export default class CopyButton extends Component {

    copyTextToClipboard(text) {
        navigator.clipboard.writeText(text).then(function() {
            // success
            document.querySelector(".btn span").textContent = "Copied :)";
            setTimeout(function() {
                document.querySelector(".btn span").textContent = "Copy URL";
            }, 2000);

        }, function(err) {
            // error
            document.querySelector(".btn span").textContent = "Could not copy. Try again";
            setTimeout(function() {
                document.querySelector(".btn span").textContent = "Copy URL";
            }, 2000);
        });
    }

    handleClick(e) {
        e.preventDefault();
        var textToCopy = "https://" + document.querySelector("#js-copy").textContent.replace(/[\n\r]+|[\s]{2,}/g, ' ').trim();
        this.copyTextToClipboard(textToCopy);
    }

    render() {
        return (
            <button className="btn" onClick={this.handleClick.bind(this)}>
                <span>Copy URL</span>
            </button >
        )
    }
}