import React, { Component } from 'react';
import './Url.css';

class Url extends Component {

    constructor(props) {
        super(props);
        this.state = {
            width: 800,
            height: 600
        }

        this.changeWidth = this.changeWidth.bind(this);
        this.changeHeight = this.changeHeight.bind(this);
    }

    changeWidth(e) {
        let value = e.target.value.replace(/\D/,'');
        let size = value.length;

        e.target.size = size > 0 ? size : 1;
        this.setState({ width: value });
    }

    changeHeight(e) {
        let value = e.target.value.replace(/\D/,'');
        let size = value.length;

        e.target.size = size > 0 ? size : 1;
        this.setState({ height: value });
    }

    render() {
        return (
            <h1 id="js-copy">
                picsum.co/
                <div className="has-indicator" data-indicator="width (max: 2000)">
                    <input type="text" className="input size" pattern="\d*" size={3} onChange={this.changeWidth} value={this.state.width} maxLength={4}/><span className="hidden">{ this.state.width }</span>
                </div>x
                <div className="has-indicator" data-indicator="height (max: 2000)">
                    <input type="text" className="input size" pattern="\d*" size={3} onChange={this.changeHeight} value={this.state.height} maxLength={4} /><span className="hidden">{ this.state.height }</span>
                </div>/
                <div className="has-indicator" data-indicator="color (6 digits)">
                <input type="text" className="input color" data-indicator="color (6 digits)" onChange={ this.props.changeColor } value={this.props.bgColor} 
                       pattern="[a-fA-F\d]+" size={6} maxLength={6} minLength={6} /><span className="hidden">{ this.props.bgColor }</span>
                </div>
            </h1>
        )
    }
}

export default Url;